@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

body {
  font-family: "Raleway", serif;
  color: white;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(/eventmeetup/static/media/travel.8b93c007.jpg) no-repeat center center;
  background-attachment: fixed;
  background-size: cover;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.eventTitle {
  font-size: 3.5em;
  font-weight: 400;
  text-shadow: 2px 2px #000000;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

h1 {
  font-size: 2em;
}

label {
  font-size: 1.5em;
  text-shadow: 2px 2px #000000;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  -webkit-align-self: center;
          align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-basis: 300px;
          flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  color: black;
  z-index: 2;
  margin: 0;
  width: 250px;
  -webkit-align-self: center;
          align-self: center;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
  background-color: rgba(99, 44, 33, 0.4);
  color: white;
  border-radius: 15px;
}

.event:hover {
  cursor: pointer;
  background-color: rgb(99, 44, 33);
}

.showDetailsButton {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
  background-color: #ffffff;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  display: inline-block;
  cursor: pointer;
  color: #632c21;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
  font-size: 1.2em;
}

.showDetailsButton:hover {
  background: linear-gradient(to bottom, #0000 5%, #0000 100%);
  background-color: #f6f6f6;
}
.showDetailsButton:active {
  position: relative;
  top: 1px;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.chartLabel {
  font-size: 2.5em;
  text-shadow: 2px 2px #000000;
  font-weight: 400;
}

.data-vis-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 6px;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: -webkit-flex;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  background: black;
  font-family: "Raleway", serif;
}

.section-title {
  font-weight: 700;
  letter-spacing: 2px;
  margin-bottom: 5%;
}

.section-description {
  font-size: 1.5em;
  margin: auto 20% auto 20%;
}

/* Navbar */
.nav-bar {
  background: white;
}

.navbar-brand {
  color: rgb(99, 44, 33) !important;
  margin-right: 20%;
  font-size: 2em;
  font-weight: bold;
}

.navbar-brand:hover {
  color: black !important;
  cursor: pointer;
}

.event-navbar li {
  display: inline;
}

.event-navbar li a {
  text-decoration: none;
  color: black;
  font-size: 1.5em;
  font-weight: bold;
  text-align: center;
  padding: 14px 16px;
}

.event-navbar li a:hover {
  color: rgb(99, 44, 33);
  font-size: 2em;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 2%;
}

.login-button {
  color: rgb(99, 44, 33);
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  font-weight: bold;
  padding: 5px;
  border: 4px solid rgb(99, 44, 33);
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.login-button:hover {
  color: #ffffff;
  background: black;
  border: 4px solid black;
}

/* Jumbotron */
.jumbotron {
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(/eventmeetup/static/media/travel.8b93c007.jpg);
  background-attachment: fixed;
  background-size: cover;
  width: 100%;
  margin-top: 7%;
}

.jumbotron-container {
  text-align: center;
  width: 60%;
}

.jumbotron h1 {
  font-size: 3rem;
  font-weight: 700;
  color: #fff;
  letter-spacing: 2px;
  margin-bottom: 5%;
  text-shadow: 2px 2px #000000;
}

.jumbotron p {
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  text-shadow: 1.5px 1.5px #000000;
  margin: auto 20% 5% 20%;
}

.jumbtron-button {
  font-weight: bold;
  font-size: 1.1em;
}

.jumbtron-button:hover {
  background-color: rgb(99, 44, 33);
  color: white;
}

/* About us */
.about-container {
  color: white;
}

/* Privacy */
.privacy-container {
  margin-top: 5%;
  background-color: white;
  color: black;
}

.privacy-description {
  margin-bottom: 2%;
}

.privacy-button,
.privacy-button:focus {
